}
.icon {
  cursor: pointer;
  width: 60px;
  height: 60px;
  position: absolute;
  right: 15px;
  top: 15px;
  border-radius: 50%;
}

/* 選單按鈕 */
#menuTrigger {
  position: absolute;
  z-index: 2;
  top: -9999px;
  left: -9999px;
}
.menuPosition {
  position: fixed;
  /* right: 0px; */
  left: 0;
  top: 0;
  height: 100%;
  z-index: 999;
}

/* 菜單 */
.menu {
  position: absolute;
  right: 0px;
  height: 100%;
  margin: 0;
  width: 300px;
  background-color: #2c3e50;
}
.menu,
.menuTrigger {
  -webkit-transition: right 0.3s linear, -webkit-transform 0.3s linear;
  transition: right 0.3s linear, -webkit-transform 0.3s linear;
  transition: transform 0.3s linear, right 0.3s linear;
  transition: transform 0.3s linear, right 0.3s linear, -webkit-transform 0.3s linear;
}
#menuTrigger:checked ~ .icon {
  right: 250px;
}
#menuTrigger:checked ~ .menuPosition {
  right: -35px;
  -webkit-animation: openMenu 1000ms 0ms;
          animation: openMenu 1000ms 0ms;
}
#menuTrigger:not(:checked) ~ .menuPosition {
  -webkit-animation: closeMenu 800ms 0ms;
          animation: closeMenu 800ms 0ms;
}

/* 決定content要不要移動 */
#menuTrigger:checked ~ .site {
  -webkit-transform: translateX(-250px);
          transform: translateX(-250px);
}
.site {
  margin: 0;
  height: 100%;
  z-index: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-family: Arial, Helvetica, sans-serif;
  color: #7f8c8d;
}
.site h1 {
  margin-top: 150px;
  text-align: center;
}

/* 菜單內的叉叉 */
.menu .bar {
  background-color: #fafafa;
}

/* 菜單內的叉叉 */
.menu .bar.first {
  -webkit-transform-origin: 50% -50%;
  transform-origin: 50% -50%;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

/* 菜單內的叉叉 */
.menu .bar.second {
  -webkit-transform-origin: 5% 0%;
  transform-origin: 5% 0%;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.menu label {
  display: block;
  position: absolute;
  width: 65px;
  height: 40px;
  right: 24px;
  cursor: pointer;
}
.menu label .bar {
  -webkit-transition: background-color 0.3s linear;
  transition: background-color 0.3s linear;
}
.menu label:hover .bar {
  background-color: red;
}
.menu ul {
  list-style-type: none;
  width: 100%;
  margin-left: 0px;
  margin-top: 50px;
}
.menu li {
  text-align: left;
  font-size: 15px;
